.btn {
  cursor: pointer;
  font: inherit;
  background-color: transparent;
  font-size: rem(13px);
  border: none;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 10px;

  &:hover{
    background-color: lightgrey;
  }
  &.warning {
    background-color: red;
  }
  &.logout{
    padding: 3px 15px;
  }
}
