.bug-form {
    margin: 100px auto;
    max-width: 500px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    padding: 8px;
  
    input {
      border: 0;
      border-bottom: 1px solid lightgray;
      padding: 8px 12px;
      background: transparent;
      margin-bottom: 5px;
    }
    button {
      padding: 8px;
      margin-bottom: 10px;
      width: 100px;
    }
    input:last-of-type{
        margin-bottom: 10px;
    }
    label{
        margin-left: 6px;
    }
  }
  