nav {
  width: 100%;
  background-color: #292929;
  box-shadow: inset 0 -6px 0 #1e1e1e;
  margin-bottom: 20px;
}

.main-logo {
  cursor: pointer;
}
.main-menu {
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    width: 130px;
  }
  & > li:first-child {
    color: #ffffff;
    font-size: 40px;
    font-weight: 400;
    letter-spacing: -0.05rem;
  }
  &> li:not(:first-child){
    border-left: 1px solid #1d1d1d;
    border-right: 1px solid #1d1d1d;
  }
  a:hover{
    color: lighten($clr1, 20%);
  }
}

a.active {
  box-shadow: -1px 0 0 #1d1d1d, inset 0 -6px 0 #58c93a;
}

